.friendList {
    display: inline-block;
    width: 300px;
    font-size: 20px;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  .friendList li {
      display: flex;
      align-items: center;
      padding: 15px;
      margin: 15px;
      border: 1px solid black;
      box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
      border-radius: 3%;
  }
  .status {

      display: block;
      width: 20px;
      height: 20px;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
  }
  .avatar {
      padding: 15px;
      margin: 0 0 0 15px;
      border-radius: 10%;
      background-color: grey;
  }
  .name {
    padding: 15px;
    font-weight: bold; 
    margin: 0;
  }
  